/**
 * 
 */
import instance from '../axios';

// 
export const getCityInfo = (cityName) => {
  return instance.get(`/area/info?name=${cityName}`)
}

// 
export const getCities = (level = 1) => {
  return instance.get(`/area/city?level=${level}`)
}

// 
export const getHotCities = () => {
  return instance.get(`/area/hot`)
}

// 
export const getCommunity = (name, id) => {
  return instance.get(`/area/community`, {
    params: {
      name,
      id
    }
  })
}

// 
export const getMapHouses = (id) => {
  return instance.get(`/area/map?id=${id}`)
}