export default class FilterPicker extends Component {
  state = {
    // 
    value: this.props.value,
  };
  render() {
    const { onCancel, onOk, data, cols } = this.props;
    return (
      <>
        {/*  */}
        <PickerView
          data={data}
          value={this.state.value}
          cols={cols}
          onChange={(val) => {
            this.setState({
              value: val,
            });
          }}
        />

        {/*  */}
        <FilterFooter
          onCancel={onCancel}
          // 
          onOk={() => {
            onOk(this.state.value);
          }}
        />
      </>
    );
  }
}
