import React, { Component } from "react";
import { NavBar, Icon, Toast } from "antd-mobile";
import { getCities, getHotCities } from "../../utils/api/city";
import { getCurryCity, setLocal, CURRY_CITY } from "../../utils";

// react-virtualized
import { List, AutoSizer } from "react-virtualized";

import "./index.scss";

class CityList extends Component {
  state = {
    cityIndex: [],
    cityList: {},
    // ,
    activeIndex: 0,
  };

  componentDidMount() {
    this.getCityList();
    // ref
    this.listRef = React.createRef();
  }

  getCityList = async () => {
    // 
    const { status, data } = await getCities();
    // 
    const res1 = await getHotCities();
    // console.log(status, data);
    // console.log(res1);
    const { cityIndex, cityList } = this.formatCities(data);
    // 
    if (res1.status === 200) {
      cityIndex.unshift("hot");
      cityList["hot"] = res1.data;
    }
    // 
    const res2 = await getCurryCity();
    // console.log(res2);

    // 
    cityIndex.unshift("#");
    cityList["#"] = [res2];

    // cityIndex.map((item) => {
    //   console.log(item, cityList[item]);
    // });
    this.setState({
      cityIndex,
      cityList,
    });
    console.log(cityIndex, cityList);
  };

  // 
  formatCities = (data) => {
    const cityList = {};
    // 
    data.forEach((item) => {
      // 
      let fst = item.short.slice(0, 1);
      // 
      if (!cityList[fst]) {
        // 
        cityList[fst] = [item];
      } else {
        // 
        cityList[fst].push(item);
      }
    });
    // console.log("", cityList); // 
    // 
    const cityIndex = Object.keys(cityList).sort();
    // console.log(cityIndex);

    return {
      cityIndex,
      cityList,
    };
  };

  // 
  formatLetter = (letter, first) => {
    switch (letter) {
      case "hot":
        return first ? "" : "";
      case "#":
        return first ? "" : "";
      default:
        return letter.toUpperCase();
    }
  };

  // 
  getRowHeight = ({ index }) => {
    const { cityIndex, cityList } = this.state;
    let letter = cityIndex[index];
    // title  +  * 
    return 36 + 50 * cityList[letter].length;
  };

  // ()
  onRowsRendered = ({ startIndex }) => {
    console.log(startIndex);
    this.setState({
      activeIndex: startIndex,
    });
  };

  //
  changeCity = (item) => {
    const hasCity = ["", "", "", ""];
    // console.log(item);
    if (hasCity.includes(item.label)) {
      localStorage.setItem(CURRY_CITY, JSON.stringify(item));
      this.props.history.goBack();
    } else {
      Toast.info("", 1);
    }
  };

  // 
  rowRenderer = ({
    key, // Unique key within array of rows
    index, // Index of row within collection
    isScrolling, // The List is currently being scrolled
    isVisible, // This row is visible within the List (eg it is not an overscanned row)
    style, // Style object to be applied to row (to position it)
  }) => {
    const { cityIndex, cityList } = this.state;
    const letter = cityIndex[index];
    return (
      <div key={key} style={style} className="city-item">
        <div className="title">{this.formatLetter(letter)}</div>
        {cityList[letter].map((item) => (
          <div
            key={item.value}
            className="name"
            onClick={() => this.changeCity(item)}
          >
            {item.label}
          </div>
        ))}
      </div>
    );
  };

  // 
  renderCityIndex = () => {
    const { cityIndex, activeIndex } = this.state;
    return cityIndex.map((item, index) => {
      return (
        <li
          key={item}
          className="city-index-item"
          onClick={() => {
            console.log(this.listRef);
            // 
            this.listRef.current.scrollToRow(index);
          }}
        >
          <span className={activeIndex === index ? "index-active" : ""}>
            {this.formatLetter(item, true)}
          </span>
        </li>
      );
    });
  };

  render() {
    return (
      <div className="cityList">
        {/*  */}
        <NavBar
          mode="light"
          icon={<Icon type="left" />}
          onLeftClick={() => this.props.history.goBack()}
        >
          
        </NavBar>
        {/*  */}
        <AutoSizer>
          {({ height, width }) => (
            <List
              ref={this.listRef}
              height={height}
              onRowsRendered={this.onRowsRendered}
              rowCount={this.state.cityIndex.length}
              rowHeight={this.getRowHeight}
              rowRenderer={this.rowRenderer}
              width={width}
            />
          )}
        </AutoSizer>
        {/*  */}
        <ul className="city-index">{this.renderCityIndex()}</ul>
      </div>
    );
  }
}

export default CityList;
